import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

const FavButton = ({ NCTId, handleClick }) => {
  const [favourite, setFavourite] = useState(false);

  useEffect(() => {
    let fav = JSON.parse(localStorage.getItem('favourite')) || [];
    if (fav.includes(NCTId)) {
      setFavourite(true);
    } else {
      setFavourite(false);
    }
    // eslint-disable-next-line
  }, []);

  const toggleSwitch = () => {
    setFavourite(!favourite); // useEffect below will fire everytime favourite is changed
  };

  useEffect(() => {
    handleClick(favourite, NCTId);
  }, [handleClick, favourite, NCTId]); // favourite as dependency, to fire only when favourite has changed

  return (
    <span
      className={`button-favourite ${favourite && `favourited`}`}
      onClick={toggleSwitch}
      id={`fav-${NCTId}`}
    >
      <i className='fa fa-heart'></i>{' '}
      {favourite ? ' Favourited' : ' Add to Favourite'}
    </span>
  );
};

FavButton.propTypes = {
  NCTId: PropTypes.string.isRequired,
  handleClick: PropTypes.func.isRequired,
};

export default FavButton;
