import React from 'react';
import PropTypes from 'prop-types';
import TextFilters from './TextFilters.js';
import { HashRouter, Link } from 'react-router-dom';

const Navbar = ({ handleConditionSearch, favCount, loading }) => {
  return (
    <div className='navigation-area'>
      <nav className='navbar navbar-expand-md col-xs-12 container'>
        <div>
          <HashRouter baseline='/'>
            <Link to='/'>
              <div className='navbar-brand'>
                <img src={require(`../../images/logo.svg`)} alt='logo' />
                <h4>TrialGate</h4>
              </div>
            </Link>
          </HashRouter>
        </div>

        <button
          className='navbar-toggler'
          type='button'
          data-toggle='collapse'
          data-target='#collapsibleNavbar'
        >
          <span className='font-weight-light'>MENU</span>
        </button>

        <div className='collapse navbar-collapse ' id='collapsibleNavbar'>
          <ul className='navbar-nav'>
            <li className='nav-item'>
              <HashRouter baseline='/'>
                <Link to='/'>
                  <span className='nav-link text-center'>
                    Browse All Trials
                  </span>
                </Link>
              </HashRouter>
            </li>

            <li className='nav-item text-center'>
              <HashRouter baseline='/'>
                <Link to='/favourites'>
                  <span className='nav-link'>
                    My Favourites {favCount > 0 && <span> ({favCount})</span>}
                  </span>
                </Link>
              </HashRouter>
            </li>
          </ul>
        </div>

        <div className='autocomplete' id='search-condition-group'>
          <TextFilters
            handleChange={handleConditionSearch}
            placeholder={'Search Condition (e.g. COVID)'}
            type={'text'}
            id={'search-condition'}
            loading={loading}
          />
        </div>
      </nav>
    </div>
  );
};

Navbar.propTypes = {
  handleConditionSearch: PropTypes.func.isRequired,
  favCount: PropTypes.number.isRequired,
  loading: PropTypes.bool.isRequired,
};

export default Navbar;
