import React from 'react';
import PropTypes from 'prop-types';

const ButtonStatus = ({ status }) => {
  return (
    <span
      className={`status-badge ${
        status === 'Recruiting'
          ? 'status-active'
          : status === 'Completed'
          ? 'status-completed'
          : 'status-inactive'
      }`}
    >
      {status}
    </span>
  );
};

ButtonStatus.propTypes = {
  status: PropTypes.string.isRequired,
};

export default ButtonStatus;
