import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { autosuggest_base_api } from '../../others/configuration.js';
import { autocomplete } from '../../others/common_methods/autocomplete.js';

const TextFilters = ({ id, handleChange, type, placeholder, loading }) => {
  const [value, setValue] = useState('');

  const updateState = () => {
    let value = document.getElementById(id).value;
    setValue(value);
    handleChange(value);
  };

  const onSubmit = (event) => {
    event.preventDefault();
    handleChange(value);
  };

  const resetInput = (event) => {
    event.preventDefault();
    setValue('');
    handleChange('');
  };

  const onChange = (event) => {
    setValue(event.target.value);
  };

  useEffect(() => {
    if (value.length > 0) {
      let full_url = autosuggest_base_api + value;
      fetch(full_url)
        .then((res) => res.json())
        .then((res) => {
          let result = res[3].reduce((arr, elem) => arr.concat(elem[0]), []); // get list of suggestions
          autocomplete(document.getElementById(id), result, updateState);
        });
    }
    // eslint-disable-next-line
  }, [value]);

  return (
    <form onSubmit={onSubmit} className='input-group'>
      <input
        className='form-control input-textbox'
        id={id}
        type={type}
        placeholder={placeholder}
        onChange={onChange}
        value={value}
        autoComplete='off'
        disabled={loading && true}
      />
      {value.length > 0 && (
        <div className='input-group-append'>
          <span onClick={resetInput} className='btn delete-btn'>
            <i className='fa fa-times'></i>
          </span>
        </div>
      )}
      <div className='input-group-append'>
        <button className='btn search-btn' type='submit'>
          <i className='fa fa-search'></i>
        </button>
      </div>
    </form>
  );
};

TextFilters.propTypes = {
  id: PropTypes.string.isRequired,
  handleChange: PropTypes.func.isRequired,
  type: PropTypes.string,
  placeholder: PropTypes.string,
  loading: PropTypes.bool.isRequired,
};

export default TextFilters;
