import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

const OptInForm = ({ NCTId, recipientList }) => {
  const [checked, setChecked] = useState(false);

  const toggleSwitch = () => {
    setChecked(!checked);
  };

  useEffect(() => {
    // disable toggle temporarily to prevent errors when double-clicked
    document.getElementById(`opt-in-${NCTId}`).disabled = true;
    setTimeout(() => {
      document.getElementById(`opt-in-${NCTId}`).disabled = false;
    }, 250);
    // eslint-disable-next-line
  }, [checked]);

  return (
    <form className='border p-3'>
      <div className='form-check'>
        <input
          onChange={toggleSwitch}
          type='checkbox'
          className='form-check-input'
          value=''
          id={`opt-in-${NCTId}`}
          data-toggle='collapse'
          data-target={`#form-${NCTId}`}
        />
        <label htmlFor={`opt-in-${NCTId}`} className='form-check-label'>
          I have read the information above and acknowledge that I fulfill all
          eligibility criteria to the best of my knowledge. I agree to be
          contacted by the study team for further screening and participation in
          this study.
        </label>
      </div>
      <div id={`form-${NCTId}`} className='collapse text-center'>
        <input
          required
          type='text'
          className='form-control form-control mt-3'
          placeholder='Name'
          name='name'
        />
        <input
          required
          type='email'
          className='form-control form-control mt-3'
          placeholder='Email'
          name='email'
        />
        <input
          required
          type='tel'
          className='form-control form-control mt-3'
          placeholder='Contact No.'
          name='contactNo'
        />
        <textarea
          className='form-control'
          placeholder='Remarks'
          rows='3'
        ></textarea>
        <hr />
        {recipientList.map((item, index) => {
          return (
            <div key={`${NCTId}-${index}`}>
              <h6 className='font-weight-bold text-left'>
                Recipient {index + 1}:
              </h6>
              <div className='input-group'>
                <input
                  type='text'
                  className='form-control'
                  value={item[0]}
                  disabled
                />
              </div>
              <div className='input-group'>
                <input
                  type='text'
                  className='form-control'
                  value={item[1]}
                  disabled
                />
              </div>
              <br />
            </div>
          );
        })}
        <button type='button' className='btn btn-primary'>
          Send to Study Team
        </button>
      </div>
    </form>
  );
};

OptInForm.propTypes = {
  NCTId: PropTypes.string.isRequired,
  recipientList: PropTypes.array.isRequired,
};

export default OptInForm;
