import React from 'react';
import PropTypes from 'prop-types';
import Filters from './Filters.js';

const openFilter = () => {
  document.getElementById('filters').click();
  window.addEventListener('popstate', closeFilter, false);
  window.history.pushState({ filter: 'open' }, ''); // write twice for it to work
  window.history.pushState({ filter: 'open' }, '');
};
const closeFilter = (event) => {
  if (event.state && event.state.filter === 'open') {
    document.getElementById('close-filters').click();
    window.removeEventListener('popstate', closeFilter, false);
  }
};

const Jumbotron = ({
  studiesFound,
  loading,
  handleStudySearch,
  handleLocationSearch,
  handleRecruitingClick,
  handleTypeClick,
  handleVolunteersClick,
  handleGenderClick,
  handleAgeGroupClick,
}) => {
  return (
    <div className='jumbotron jumbotron-fluid'>
      <div className='container'>
        <div className='breadcrumbs'>
          {loading
            ? 'Connecting to ClinicalTrials.gov database...'
            : 'Data Sourced from ClinicalTrials.gov'}
        </div>
        <h1 className='title'>
          {loading
            ? 'Searching for Studies...'
            : `${studiesFound.toLocaleString()} ${
                studiesFound > 1 ? 'Studies' : 'Study'
              } Found`}
        </h1>
        <div>
          <button
            className='btn btn-primary'
            type='button'
            data-toggle='modal'
            data-target='#filters'
            onClick={openFilter}
          >
            <i className='fa fa-filter'></i> Filters
          </button>
        </div>
        <Filters
          loading={loading}
          handleStudySearch={handleStudySearch}
          handleLocationSearch={handleLocationSearch}
          handleRecruitingClick={handleRecruitingClick}
          handleTypeClick={handleTypeClick}
          handleVolunteersClick={handleVolunteersClick}
          handleGenderClick={handleGenderClick}
          handleAgeGroupClick={handleAgeGroupClick}
          handleCloseFilters={openFilter}
        />
      </div>
    </div>
  );
};

Jumbotron.propTypes = {
  studiesFound: PropTypes.number.isRequired,
  loading: PropTypes.bool.isRequired,
  handleStudySearch: PropTypes.func.isRequired,
  handleLocationSearch: PropTypes.func.isRequired,
  handleRecruitingClick: PropTypes.func.isRequired,
  handleTypeClick: PropTypes.func.isRequired,
  handleVolunteersClick: PropTypes.func.isRequired,
  handleGenderClick: PropTypes.func.isRequired,
  handleAgeGroupClick: PropTypes.func.isRequired,
};

export default Jumbotron;
