import React, { useEffect, useReducer } from 'react';
import Navbar from '../elements/Navbar.js';
import Jumbotron from '../elements/Jumbotron.js';
import Spinner from '../elements/Spinner.js';
import LoadMoreBtn from '../elements/LoadMoreBtn.js';
import ScrollUpButton from 'react-scroll-up-button';
import Card from '../elements/Card.js';
import { record_size, base_api } from '../../others/configuration.js';
import initialState from './initialState';

const getRecruitingStatus = () => {
  const recruitingStatus = document.getElementById('status-recruiting');
  const notRecruitingStatus = document.getElementById('status-not-recruiting');
  let result = [];
  recruitingStatus.checked && result.push('Recruiting');
  notRecruitingStatus.checked && result.push('NOT Recruiting');
  return result.join('+OR+');
};

const getStudyType = () => {
  const interventionalType = document.getElementById('type-interventional');
  const observationalType = document.getElementById('type-observational');
  let result = [];
  interventionalType.checked && result.push('Interventional');
  observationalType.checked && result.push('Observational');
  if (result.length === 2) result = []; // include both types if both checkboxes are selected
  return result.join('+OR+');
};

const getHealthyVolunteers = () => {
  const volunteersYes = document.getElementById('healthy-volunteers-yes');
  const volunteersNo = document.getElementById('healthy-volunteers-no');
  let result = [];
  volunteersYes.checked && result.push('Accepts Healthy Volunteers');
  volunteersNo.checked && result.push('NOT Accept Healthy Volunteers');
  return result.join('+OR+');
};

const getGenders = () => {
  const genderMale = document.getElementById('gender-male');
  const genderFemale = document.getElementById('gender-female');
  let result = [];
  genderMale.checked && result.push('Male');
  genderFemale.checked && result.push('Female');
  if (result.length === 2) result = []; // include both genders if both checkboxes are selected
  return result.join('+OR+');
};

const getAgeGroups = () => {
  const ageChild = document.getElementById('age-child');
  const ageAdult = document.getElementById('age-adult');
  const ageOldAdult = document.getElementById('age-old-adult');
  let result = [];
  ageChild.checked && result.push('Child');
  ageAdult.checked && result.push('Adult');
  ageOldAdult.checked && result.push('Older Adult');
  if (result.length === 3) result = []; // include all age groups if all checkboxes are selected
  return result.join('+OR+');
};

const reducer = (state, action) => {
  switch (action.type) {
    case 'FETCH_ITEMS':
      return {
        ...state,
        loading: false,
        studiesFound: action.result.FullStudiesResponse.NStudiesFound,
        trials: action.result.FullStudiesResponse.FullStudies,
        NCTId: action.fav,
      };

    case 'SEARCH_CONDITION':
      return {
        ...state,
        loading: true,
        ConditionSearch: action.searchTerm,
        min_rnk: 1,
        max_rnk: record_size,
        trials: [],
      };

    case 'SEARCH_STUDY':
      return {
        ...state,
        loading: true,
        OfficialTitle: action.searchTerm,
        min_rnk: 1,
        max_rnk: record_size,
        trials: [],
      };

    case 'SEARCH_LOCATION':
      return {
        ...state,
        loading: true,
        LocationSearch: action.searchTerm,
        min_rnk: 1,
        max_rnk: record_size,
        trials: [],
      };

    case 'CHECK_RECRUITMENT_STATUS':
      const recruitingStatus = getRecruitingStatus();
      return {
        ...state,
        loading: true,
        OverallStatus: recruitingStatus,
        min_rnk: 1,
        max_rnk: record_size,
        trials: [],
      };

    case 'CHECK_STUDY_TYPE':
      const studyType = getStudyType();
      return {
        ...state,
        loading: true,
        StudyType: studyType,
        min_rnk: 1,
        max_rnk: record_size,
        trials: [],
      };

    case 'CHECK_HEALTHY_VOLUNTEERS':
      const healthyVolunteers = getHealthyVolunteers();
      return {
        ...state,
        loading: true,
        HealthyVolunteers: healthyVolunteers,
        min_rnk: 1,
        max_rnk: record_size,
        trials: [],
      };

    case 'CHECK_GENDER':
      const gender = getGenders();
      return {
        ...state,
        loading: true,
        Gender: gender,
        min_rnk: 1,
        max_rnk: record_size,
        trials: [],
      };

    case 'CHECK_AGE_GROUP':
      const ageGroup = getAgeGroups();
      return {
        ...state,
        loading: true,
        StdAge: ageGroup,
        min_rnk: 1,
        max_rnk: record_size,
        trials: [],
      };

    case 'STOP_LOADING':
      return {
        ...state,
        loading: false,
      };

    case 'LOAD_MORE':
      return {
        ...state,
        loading: true,
        min_rnk: state.min_rnk + record_size,
        max_rnk: state.max_rnk + record_size,
      };

    case 'CONCAT_TRIALS':
      return {
        ...state,
        loading: false,
        trials: [
          ...state.trials,
          ...action.newTrials.FullStudiesResponse.FullStudies,
        ],
      };
    default:
      return state;
  }
};

const App = () => {
  const [state, dispatch] = useReducer(reducer, initialState);

  const handleConditionSearch = (searchTerm) =>
    dispatch({ type: 'SEARCH_CONDITION', searchTerm });
  const handleStudySearch = (searchTerm) =>
    dispatch({ type: 'SEARCH_STUDY', searchTerm });
  const handleLocationSearch = (searchTerm) =>
    dispatch({ type: 'SEARCH_LOCATION', searchTerm });
  const handleRecruitingClick = () =>
    dispatch({ type: 'CHECK_RECRUITMENT_STATUS' });
  const handleTypeClick = () => dispatch({ type: 'CHECK_STUDY_TYPE' });
  const handleVolunteersClick = () =>
    dispatch({ type: 'CHECK_HEALTHY_VOLUNTEERS' });
  const handleGenderClick = () => dispatch({ type: 'CHECK_GENDER' });
  const handleAgeGroupClick = () => dispatch({ type: 'CHECK_AGE_GROUP' });
  const handleLoadMore = () => dispatch({ type: 'LOAD_MORE' });

  const apiGenerator = (state) => {
    const api_base_url = base_api;
    const keyPrefix = 'AREA%5B';
    const keySuffix = '%5D';
    const exprPrefix = '%28';
    const exprSuffix = '%29';
    let expr = '';
    for (let key in state) {
      if (
        key[0] === key[0].toUpperCase() &&
        state[key] !== '' &&
        key !== 'NCTId'
      ) {
        if (key === 'OverallStatus') {
          expr += `${keyPrefix}${key}${keySuffix}COVERAGE%5BFullMatch%5D${exprPrefix}${state[key]}${exprSuffix}+AND+`; // special handling to filter out terms like 'not yet recruiting'
        } else {
          expr += `${keyPrefix}${key}${keySuffix}${exprPrefix}${state[key]}${exprSuffix}+AND+`;
        }
      }
    }
    if (expr.length > 0) expr = expr.substr(0, expr.length - 5); //remove final '+AND+'
    return `${api_base_url}&expr=${expr}&min_rnk=${state.min_rnk}&max_rnk=${state.max_rnk}&fmt=json`;
    // take note of initialState above
  };

  const fetchItems = async (api_url) => {
    let fav = JSON.parse(localStorage.getItem('favourite')) || [];
    try {
      const result = await (await fetch(api_url)).json();
      dispatch({
        type: 'FETCH_ITEMS',
        result,
        fav,
      });
    } catch (error) {
      console.log(`Error fetching ${api_url}: ${error}`);
    }
  };

  // respond to filter changes
  useEffect(() => {
    let api_url = apiGenerator(state);
    fetchItems(api_url);
    // eslint-disable-next-line
  }, [
    state.ConditionSearch,
    state.OfficialTitle,
    state.LocationSearch,
    state.OverallStatus,
    state.StudyType,
    state.HealthyVolunteers,
    state.Gender,
    state.StdAge,
  ]);

  // respond to load more
  useEffect(() => {
    if (state.min_rnk <= state.studiesFound) {
      let api_url = apiGenerator(state);
      fetch(api_url)
        .then((res) => res.json())
        .then((res) => {
          dispatch({ type: 'CONCAT_TRIALS', newTrials: res });
        });
    }
    // eslint-disable-next-line
  }, [state.min_rnk]);

  return (
    <div className='container-fluid p-0'>
      <ScrollUpButton
        ContainerClassName='AnyClassForContainer'
        TransitionClassName='AnyClassForTransition'
      />
      <Navbar
        handleConditionSearch={handleConditionSearch}
        favCount={state.NCTId.length}
        loading={state.loading}
      />
      <Jumbotron
        studiesFound={state.studiesFound}
        loading={state.loading}
        handleConditionSearch={(searchTerm) =>
          dispatch({ type: 'SEARCH_CONDITION', searchTerm })
        }
        handleStudySearch={handleStudySearch}
        handleLocationSearch={handleLocationSearch}
        handleRecruitingClick={handleRecruitingClick}
        handleTypeClick={handleTypeClick}
        handleVolunteersClick={handleVolunteersClick}
        handleGenderClick={handleGenderClick}
        handleAgeGroupClick={handleAgeGroupClick}
      />
      {state.trials !== undefined ? (
        <div className='container'>
          <div className='row'>
            <div
              className={`border content ${
                state.loading && state.min_rnk === 1 && 'content-loading'
              }`}
            >
              <table className='table'>
                <tbody>
                  {state.trials
                    .filter(
                      (trial) =>
                        trial.Study.ProtocolSection.StatusModule
                          .OverallStatus !== 'Withheld'
                    )
                    .map((trial, index) => (
                      <Card study={trial.Study} index={index} key={index} />
                    ))}
                </tbody>
              </table>
            </div>

            {/* sidebar */}
            <div>{/* to render sidebar here */}</div>
          </div>
          {state.loading ? (
            <Spinner />
          ) : state.studiesFound > state.max_rnk ? (
            <LoadMoreBtn handleClick={handleLoadMore} />
          ) : (
            <div className='container text-center mt-4 mb-5'>
              <span>All records shown</span>
            </div>
          )}
        </div>
      ) : (
        <div className='container text-center not-found'>
          <img src={require(`../../images/not-found.jpg`)} alt='not-found' />
          <br />
          <span>No data found</span>
          <br />
          <span>Please try a different search criteria</span>
        </div>
      )}
    </div>
  );
};

export default App;
